
import { getCurrentInstance } from "@vue/runtime-core"
export default {
  name: "Setting",
  setup() {
    // @ts-ignore
    const { ctx } = getCurrentInstance()
    const layout = function () {
      localStorage.clear()
      sessionStorage.clear()
      ctx.$router.replace('/login')
      ctx.$message.info('退出成功')
    }
    return { layout }
  }
}
